import { basicSetup, EditorState, EditorView } from '@codemirror/basic-setup';
import { javascript } from '@codemirror/lang-javascript';

const theme = EditorView.theme({
  '&': {
    fontSize: '16pt',
  },
  '.cm-content': {
    fontFamily: 'Source Code Pro',
  },
});

const initialState = EditorState.create({
  doc: `function x() {} function x() {} function x() {} function x() {}
  
  const x=2  function x() {} function x() {} function x() {} function x() {}
    
  const x=2  
  
  function x() {} function x() {} function x() {} function x() {}
    
  const x=2  
  
  function x() {} function x() {} function x() {} function x() {}
    
  const x=2  
  
  function x() {} function x() {} function x() {} function x() {}
    
  const x=2  
  
  function x() {} function x() {} function x() {} function x() {}
    
  const x=2  
  
  
  function x() {} function x() {} function x() {} function x() {}
    
  const x=2  
  
  function x() {} function x() {} function x() {} function x() {}
    
  const x=2  
  
  function x() {} function x() {} function x() {} function x() {}
    
  const x=2  
  `,
  extensions: [basicSetup, javascript(), EditorView.lineWrapping, theme],
});

const view = new EditorView({
  parent: document.getElementById('editor'),
  state: initialState,
});

window.view = view;
